import "~/extension/HC2DDom";
import "~/extension/Hc2DGui";
import "~/extension/HC2DTexture";

import { AppConfig, Application, AppStore } from "@hc/engine";
import * as HC from "@hc/engine";
import React, { useEffect, useRef, useState } from "react";
import { createRoot } from "react-dom/client";

import axios from "axios";
import { AppApi, FileApi } from "./apis";
import { getFunction, registerComponent } from "./utils/component";
import { Box } from "@mui/material";
import { LinearProgressWithLabel2 } from "./components/common/circularProgressWithLabel";
import { sleep } from "./utils/utils";
import checkVerUpdate from "./utils/checkVerUpdate";

interface PreviewProps {}

checkVerUpdate();

AppConfig.assetURL = ASSET_URL;
AppConfig.Texture_URL = "https://am-img.gkiiot.com/editor/";
AppConfig.SkyBox_URL = "https://am-img.gkiiot.com/editor/";

const intance = axios.create({ baseURL: API_URL });
let key: string;
let app: Application;

const Share: React.FC<PreviewProps> = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [confirm, setConfirm] = useState(true);
  const [url2d, setUrl2d] = useState("");
  const [pwd, setPwd] = useState("");
  const [progress, setProgress] = useState({
    start: false,
    value: 0,
    label: "",
  });

  const vaildPwd = () => {
    vail(true);
  };

  const vail = async (log = false) => {
    const res = await intance.post(AppApi.checkShare, { key, secret: pwd });

    let json = res.data.data.data;
    const variables = res.data.variables;

    if (variables) {
      AppStore.GlobalVariables = JSON.parse(variables);
    }

    AppConfig.assetURL = res.data.data.prefix;
    if (json) {
      setConfirm(true);
      const data = JSON.parse(json);
      const components = data.components ?? [];
      for (const id of components) {
        const res = await intance.get(FileApi.detail2 + id);
        console.log(res);
        if (res["status"] === 200 && res.data.code === 0) {
          const json = res.data.data.json_data;
          const data = JSON.parse(json);
          registerComponent({ ...data.data, id });
        }
      }
      app.loadFile(data.data);
      const scripts = data.scripts ?? [];
      for (const script of scripts) {
        const f = getFunction(script.code)();
        f(app, HC);
      }
      setUrl2d(data.url2d);
    } else {
      setConfirm(false);
      if (log) {
        alert("密码错误");
      }
    }
  };

  useEffect(() => {
    const urlSeach = new URLSearchParams(location.search);
    key = urlSeach.get("k");
    vail();
  }, []);

  useEffect(() => {
    (async () => {
      if (canvasRef.current) {
        const canvas = canvasRef.current;
        window["app"] = app = new Application({
          canvas,
          editor: false,
          cameraRadius: 100,
        });

        app.on("loadingBackground", (event) => {
          setProgress((progress) => {
            let value = progress.value + 5;
            if (value > 90) value = progress.value;
            return {
              ...progress,
              value: value,
              label: `正在加载天空图:`,
            };
          });
        });
        app.on("loadBackground", (event) => {
          setProgress((progress) => {
            return {
              start: false,
              value: 0,
              label: `天空图加载完成`,
            };
          });
        });

        app.on("loading", async (event) => {
          await sleep(100);
          setProgress((progress) => {
            return {
              start: true,
              value: event.percentage,
              label:
                event.percentage === 100
                  ? `整理资源`
                  : `正在加载${event.target?.Name}`,
            };
          });
        });
        app.on("load", async (event) => {
          await sleep(100);

          setProgress((progress) => {
            return {
              start: false,
              value: 100,
              label: `加载${event.target?.Name}完成`,
            };
          });
        });
      }
    })();
  }, []);

  return (
    <>
      {!confirm && (
        <div
          style={{
            position: "fixed",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            margin: "auto",
            width: "350px",
            height: "30px",
            border: "1px solid #ccc",
            display: "flex",
            padding: 20,
            alignItems: "center",
          }}
        >
          <label>请输入密码:</label>
          <input
            value={pwd}
            type="password"
            style={{ margin: "0 20px", height: 30 }}
            onChange={(evt) => setPwd(evt.target.value)}
          />
          <button onClick={vaildPwd} style={{ padding: 5 }}>
            确认
          </button>
        </div>
      )}
      <Box
        style={{
          width: "100%",
          height: "100%",
          position: "relative",
        }}
      >
        {/* <iframe
          src={url2d}
          style={{
            width: "100%",
            height: "100%",
            pointerEvents: "none",
            backgroundColor: "transparent",
            border: "0",
            position: "fixed",
          }}
        ></iframe> */}
        <canvas style={{ width: "100%", height: "100%" }} ref={canvasRef}>
          分享页面
        </canvas>
        {progress.start && (
          <Box
            style={{
              position: "absolute",
              left: 0,
              top: 0,
              width: "100%",
              height: "100%",
              backgroundColor: "rgba(0,0,0,0.7)",
              color: "#FCFCFC",
            }}
          >
            <Box
              style={{
                position: "absolute",
                top: "50%",
                left: "50%",
                width: "50%",
                transform: "translate(-50%, -50%)",
              }}
            >
              <LinearProgressWithLabel2
                sx={{ height: 15, borderRadius: 5 }}
                value={progress.value}
                label={progress.label}
              />
            </Box>
          </Box>
        )}
      </Box>
    </>
  );
};

const root = createRoot(document.getElementById("app"));

root.render(<Share />);
